.frame95{
    position: absolute;
    width: 300px;
    height: 850px;
    left: 0px;
    top: 0px;
    background: #23304C;
}

.frame95 h1{
    position: absolute;
    left: 10.33%;
    right: 16.67%;
    top: 5.23%;
    bottom: 87.46%;
    font-size: 52px;
    color: #EEEEEE;
    font-family: 'Times New Roman', Times, serif;
}

.frame94{
    position: absolute;
    width: 175px;
    height: 531px;
    left: 41px;
    top: 209px;
}

.frame94 ul{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.frame94 ul li:nth-child(7)::before{
    content: "H";
    color: #FBE165;
    font-size: 2px;
    position: absolute;
    width: 12px;
    height: 12px;
    left: -47px;
    margin-top: 5px;
    border-radius: 50%;
    background: #FBE165;
    box-shadow: 3px 3px 15px #FBE165;
}

.frame94 ul li{
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.5px;
    color: #A8B0BC;
}





